@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/Montserrat-Black.woff2") format("woff2");
  font-display: swap;
}

body {
  margin: 0;
}

main {
  flex-grow: 1;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes levitate {
  0% {
    transform: translateY(15px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes move-girl {
  100% {
    transform: translateY(0);
  }
}

@keyframes move-gap {
  from {
    opacity: 0.5;
  }

  25% {
    opacity: 0.6;
  }

  50% {
    width: 280px;
    opacity: 0.8;
  }

  to {
    opacity: 1;
  }
}

.animation {
  position: relative;
  align-self: center;
  overflow: hidden;
}

.animation__gap {
  position: relative;
  width: 193px;
  height: 80px;
  margin: 0 auto;
}

.animation__gap::after {
  content: "";
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 193px;
  height: 161px;
  background-image: url("../images/svg-sprites/sprite.svg#ellipse");
  background-repeat: no-repeat;
  animation-name: move-gap;
  animation-duration: 6.2s;
  animation-timing-function: easeOutSine;
  z-index: -1;
}

.animation__girl {
  position: relative;
  width: 289px;
  height: 756px;
  animation-name: levitate;
  animation-duration: 2s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  z-index: 1;
}

.animation__girl img {
  transform: translateY(843px);
  animation-name: move-girl;
  animation-duration: 3s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

.animation__layer {
  position: absolute;
  top: 775px;
  left: 50%;
  width: 320px;
  height: 830px;
  transform: translate(-50%, 0);
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../images/rectangle.png");
  z-index: 2;
}

.container {
  padding: 0.9rem;
}

.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.6875;
  color: #FFFFFF;
  background-color: #181818;
}

.voice {
  display: flex;
  flex-direction: column;
  z-index: 2;
}

.voice__title {
  margin: 0;
  margin-bottom: 2rem;
  font-size: 2.125rem;
  line-height: 1;
  font-weight: 400;
}

.voice__text {
  margin: 0;
}

.voice__text + .voice__text {
  margin-top: 2rem;
}

.voice__img {
  align-self: center;
  width: 16.4rem;
  height: 390px;
  margin-top: 3rem;
}

.voice__img img {
  transform: translateX(3px) scale(1.3);
}

.voice__say {
  margin: 0;
  margin-bottom: 0.5rem;
  font-size: 2.375rem;
  line-height: 0.9210526316;
}

.voice__yamaguchi {
  margin: 0;
  font-size: 3.8rem;
  line-height: 0.9016393443;
  z-index: 5;
}

.voice__yamaguchi span {
  font-size: 3.125rem;
  line-height: 0.88;
}

@media (min-width: 1200px) {
  .animation {
    grid-column: 2/3;
    grid-row: 1/6;
    justify-self: center;
    transform: translateX(45px);
  }

  .animation__girl {
    width: 435px;
    height: auto;
    text-align: center;
  }

  .animation__layer {
    top: 780px;
  }

  .container {
    padding: 1.5rem 4rem;
  }

  .voice {
    display: grid;
    grid-template-columns: 36% auto auto;
    grid-template-rows: repeat(4, min-content) 1fr;
    column-gap: 1rem;
  }

  .voice__title,
  .voice__text,
  .voice__say {
    grid-column: 1/2;
  }

  .voice__title {
    width: 70%;
    margin: 4rem 0;
    font-size: 3.5rem;
    line-height: 1.0714285714;
  }

  .voice__text {
    font-size: 1.375rem;
    line-height: 1.6818181818;
  }

  .voice__text + .voice__text {
    margin-top: 1.2rem;
  }

  .voice__say {
    margin: 2.5rem 0;
    font-size: 2.8rem;
    line-height: 0.8888888889;
  }

  .voice__yamaguchi {
    grid-column: 1/3;
    grid-row: 5/6;
    margin-left: 3rem;
  }

  .voice__yamaguchi span {
    font-size: 3.8rem;
    line-height: 0.9016393443;
  }

  .voice__img {
    grid-column: 3/4;
    grid-row: 1/6;
    width: 500px;
    height: auto;
  }

  .voice__img img {
    transform: translateX(60px) scale(1);
  }
}

@media (min-width: 1400px) {
  .animation__overlay {
    z-index: 3;
    width: 1400px;
    height: 200px;
    position: absolute;
    bottom: -11%;
    left: -171%;
    mask-image: url("../images/svg-sprites/sprite.svg#bg");
  }

  .voice__img {
    width: 600px;
  }
}